//—Ñ—É–Ω–∫—Ü—ñ—è –ø–µ—Ä–µ–±–æ—Ä—É –º–∞—Å–∏–≤–∞ —Å–≤—ñ—Ç–ª–∏–Ω –∑ –ø—ñ–¥—Å—Ç–∞–Ω–æ–≤–æ—é –≤ —à–∞–±–ª–æ–Ω–Ω—É —Å—Ç—Ä–æ–∫—É
export default function createItemCadrGallery(e) {
  return e
    .map(({ preview, original, description }) => {
      return `<li class="gallery__item">
      <a
        class="gallery__link"
        href="#${preview}"
      >
        <img
          class="gallery__image"
          src="${preview}"
          data-source="${original}"
          alt="${description}"
        />
      </a>
    </li> `;
    })
    .join("");
}
